.card{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 260px;
    position: relative;
}

.image{
    position: static;
    width: 240px;
    height: 120px;
    left: 16px;
    top: 0px;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 8px 0px;
    cursor: pointer;
}
.image img{ width: 100%;}

.price{
    display: flex;
    justify-content: center;
    font-family: 'Iceland';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 24px;
    /* identical to box height, or 86% */
    text-align: center;
}
.name{
    font-family: 'Jet Brains Mono';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    margin: 8px 0px;
    height: 24px;
    left: 0%;
    right: 0%;
    bottom: 24px;
}